                    <div style="text-align: left">
                      <ul>
                        <li style="color: red">30-DAY成長補給品は全てコアダストに変換するものとして計算</li>
                        <li>基地防御報酬の数値はゲーム内と僅かに違いあり</li>
                        <li>資源バランスを考慮した課金の最適化はしていないため、計算結果の課金額は参考程度とする事</li>
                        <li>ウィークリーやマンスリー商品の課金計上は次の火曜ないし1日を迎えたところで行うため、週や月を跨がずにレベルアップする場合は課金額が0になる場合がある</li>
                      </ul>
