      let dstNikkeTop = 0;
      let dstNikkeLeft = 0;

      // ドロップ先がニケ画像ならドロップ先をニケ画像の親に置き換え（グラフが親になる想定）
      if (e.target.id.includes('nikke')) {
        drop = e.target.parentNode;
        dstNikkeTop = Number(e.target.style.top.replace('px',''));
        dstNikkeLeft = Number(e.target.style.left.replace('px',''));
      }

      if (drop.id.startsWith('smGraph')) {
        var rect = document.getElementById(target.id).getBoundingClientRect();
        var height = rect.height;
        var width = rect.width;

        drop.insertBefore(target, drop.childNodes.item(0));
        target.style.top = e.offsetY + dstNikkeTop - height/2 + "px";
        target.style.left = e.offsetX + dstNikkeLeft - width/2 + "px";
