        //console.log("height=" + height + " width=" + width);
      } else if (drop.id.startsWith('smCandidate')) {
        let idx = 0; //drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
        let cnt = 0;
        // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
        drop.childNodes.forEach(function(item){
          if(item.id <= target.id) {
            idx = cnt+1;
          }
          cnt++;
        });

        drop.insertBefore(target, drop.childNodes.item(idx)); // ドロップ先画像の1つ前に追加

        // ドロップした画像のstyleを元に戻す
        target.style.top = "0px";
        target.style.left = "0px";
        target.style.zIndex = 1;
        target.style.position = "relative";

