          let cnt = 0;
          // ドロップ先の画像が部隊の枠の中の何番目の要素なのか探す
          drop.childNodes.forEach(function(item){
            if(item.id == e.target.id) {
              idx = cnt;
            }
            cnt++;
          });

          // ドロップ元と先が同一部隊内の場合、ドロップ元の要素番号を探す
          let src = 0; // ドロップ元の要素番号
          cnt = 0;
          if(drop.id == target.parentNode.id) {
            drop.childNodes.forEach(function(item){
              if(item.id == target.id) {
                src = cnt;
              }
              cnt++;
            });
            if(src < idx) { // ドロップ元要素番号<ドロップ先要素番号の場合、挿入先をドロップ先要素の後ろにする
