  methods: {
    mLsPackCheck(id, chkOn) {
      let _this = this;
      this.packs.forEach(function(pack){
        if(pack.id == id) {
          pack.check = pack.check * (-1);

          if (chkOn) {
            if (_this.selected[pack.id] === undefined) {
              _this.selected[pack.id] = true;
            } else {
              _this.selected[pack.id] = !_this.selected[pack.id];
            }
          }
        }
      })
      this.mCalcLeveling();

      //console.log("--------------------------");
      //for (let i=0; i <= this.selected.length; ++i) {
      //  if (!(this.selected[i] === undefined)) {
      //    console.log("i=" + i + " " + this.selected[i]);
      //  }
      //}
    },

    mCalcLeveling() { // レベリング計算
      let _this = this;

      this.needs[0].NextLv = this.coNikkeLv + 1;
      this.needs[1].NextLv = Math.floor(this.coNikkeLv / 20) * 20 + 21;
      this.needs[2].NextLv = this.needs[1].NextLv + 20;

      // リスト上でチェックされたパックから追加分の資源を読み取り
      let creditDayNum = 0;
      let creditWeekNum = 0;
      let creditMonthNum = 0;
      let creditDayCase = 0;
      let creditWeekCase = 0;
      let creditMonthCase = 0;
      let coreDayNum = 0;
      let coreWeekNum = 0;
      let coreMonthNum = 0;
      let coreDayCase = 0;
      let coreWeekCase = 0;
      let coreMonthCase = 0;
      let battleDayNum = 0;
      let battleWeekNum = 0;
      let battleMonthNum = 0;
      let battleDayCase = 0;
      let battleWeekCase = 0;
      let battleMonthCase = 0;
      this.packs.forEach(function(pack){
        if (pack.check == true) {
          if (pack.division == "デイリー") {
            creditDayCase += pack.items.CaseCredit;
            creditDayNum += pack.items.Credit * 1000;
            coreDayCase += pack.items.CaseCore;
            coreDayNum += pack.items.Core;
            battleDayCase += pack.items.CaseBattle;
            battleDayNum += pack.items.Battle * 1000;
            //yenDay += pack.price;
          } else if (pack.division == "ウィークリー") {
            creditWeekCase += pack.items.CaseCredit;
            creditWeekNum += pack.items.Credit * 1000;
            coreWeekCase += pack.items.CaseCore;
            coreWeekNum += pack.items.Core;
            battleWeekCase += pack.items.CaseBattle;
            battleWeekNum += pack.items.Battle * 1000;
            //yenWeek += pack.price;
          } else if (pack.division == "マンスリー") {
            creditMonthCase += pack.items.CaseCredit;
            creditMonthNum += pack.items.Credit * 1000;
            coreMonthCase += pack.items.CaseCore;
            coreMonthNum += pack.items.Core;
            battleMonthCase += pack.items.CaseBattle;
            battleMonthNum += pack.items.Battle * 1000;
            //yenMonth += pack.price;
          } else if (pack.name == "30-DAY成長補給品") {
            coreDayCase += 10;
          }
        }
      });
      //console.log("------------------------------")
      //console.log("コアダスト課金収入/日=" + coreDay)
      //console.log("コアダスト課金収入/週=" + coreWeek)
      //console.log("コアダスト課金収入/月=" + coreMonth)
      //console.log("コアダスト報酬/h=" + this.coRewardCore);

      // 経験値リストから現在レベルの蓄積経験値と目標レベルの蓄積経験値を取得
      var expNow = this.experience.find(e => e.Lv === this.coNikkeLv);

      if (this.needs[0].NextLv <= this.experience.length) {
        var expNext1 = this.experience.find(e => e.Lv === this.needs[0].NextLv);
        this.needs[0].NeedCredit = expNext1.Credit - expNow.Credit - (this.lsPosCaseCredit * this.coRewardCredit) - (this.lsPosCredit * 1000);
        this.needs[0].NeedBattle = expNext1.Battle - expNow.Battle - (this.lsPosCaseBattle * this.coRewardBattle) - (this.lsPosBattle * 1000);
        this.needs[0].NeedCore   = expNext1.Core   - expNow.Core   - (this.lsPosCaseCore   * this.coRewardCore)   - this.lsPosCore;
      }
      
      if (this.needs[1].NextLv <= this.experience.length) {
        var expNext20 = this.experience.find(e => e.Lv === this.needs[1].NextLv);
        this.needs[1].NeedCredit = expNext20.Credit - expNow.Credit - (this.lsPosCaseCredit * this.coRewardCredit) - (this.lsPosCredit * 1000);
        this.needs[1].NeedBattle = expNext20.Battle - expNow.Battle - (this.lsPosCaseBattle * this.coRewardBattle) - (this.lsPosBattle * 1000);
        this.needs[1].NeedCore   = expNext20.Core   - expNow.Core   - (this.lsPosCaseCore   * this.coRewardCore)   - this.lsPosCore;
      }
      
      if (this.needs[2].NextLv <= this.experience.length) {
        var expNext40 = this.experience.find(e => e.Lv === this.needs[2].NextLv);
        this.needs[2].NeedCredit = expNext40.Credit - expNow.Credit - (this.lsPosCaseCredit * this.coRewardCredit) - (this.lsPosCredit * 1000);
        this.needs[2].NeedBattle = expNext40.Battle - expNow.Battle - (this.lsPosCaseBattle * this.coRewardBattle) - (this.lsPosBattle * 1000);
        this.needs[2].NeedCore   = expNext40.Core   - expNow.Core   - (this.lsPosCaseCore   * this.coRewardCore)   - this.lsPosCore;
      }
      //console.log("NeedCredit1=" + this.needs[0].NeedCredit)
      //console.log("NeedBattle1=" + this.needs[0].NeedBattle)
      //console.log("NeedCore1=" + this.needs[0].NeedCore)
      //console.log("NeedCredit20=" + this.needs[1].NeedCredit)
      //console.log("NeedBattle20=" + this.needs[1].NeedBattle)
      //console.log("NeedCore20=" + this.needs[1].NeedCore)

      // クレジットの所要時間（次レベルまでの残り時間を計算）
      let flgCredit = false;
      let flgBattle = false;
      let flgCore = false;
      let lpCnt = -1;
      if (this.needs[0].NextLv <= this.experience.length) lpCnt++; // 次のレベルがデータテーブルの範囲内
      if (this.needs[1].NextLv <= this.experience.length) lpCnt++; // 20レベル区切りの次のレベルがデータテーブルの範囲内
      if (this.needs[2].NextLv <= this.experience.length) lpCnt++; // 20レベル区切りの次の次のレベルがデータテーブルの範囲内

      let log = 2;

      for (let i = 0; i <= lpCnt; ++i) {
        let nextday = new Date();
        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].NeedYen = 0;
        while (this.needs[i].NeedCredit >= 0 || this.needs[i].NeedBattle >= 0 || this.needs[i].NeedCore >= 0) {
          nextday.setHours(nextday.getHours() + 1); // +1h
          flgCredit = false;
          flgBattle = false;
          flgCore = false;

          //if (i == log) console.log(nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")");

          // クレジット計算
          if (this.needs[i].NeedCredit >= 0) {
            flgCredit = true;
            this.needs[i].HourCredit++; // 残り時間を+1
            this.needs[i].NeedCredit -= this.coRewardCredit; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditDayCase * Math.floor(this.coRewardCredit)) // デイリー系課金アイテム(ケース)
                                         + creditDayNum // デイリー系課金アイテム(個)
                                         + Math.floor(this.lsWipeouts * this.coRewardCredit * 2) // まとめて殲滅
                                         + (1 * Math.floor(this.coRewardCredit)); // デイリーミッション
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditWeekCase * Math.floor(this.coRewardCredit)); // ウィークリー系課金アイテム(ケース)
                                        + creditWeekNum // ウィークリー系課金アイテム(個)
                                        + Math.floor((8 * this.coRewardCredit)) * 2 // ウィークリーミッション
                                        + (18 * Math.floor(this.coRewardCredit) + 500000); // 鉄くずショップ
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditMonthCase * Math.floor(this.coRewardCredit)) // マンスリー系課金アイテム(ケース)
                                        + creditMonthNum; // マンスリー系課金アイテム(個)
            }
          }
          
          // バトルデータ計算
          if (this.needs[i].NeedBattle >= 0) {
            flgBattle = true;
            this.needs[i].HourBattle++; // 残り時間を+1
            this.needs[i].NeedBattle -= this.coRewardBattle; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleDayCase * Math.floor(this.coRewardBattle)) // デイリー系課金アイテム(ケース)
                                        + battleDayNum // デイリー系課金アイテム(個)
                                        + Math.floor(this.lsWipeouts * this.coRewardBattle * 2) // まとめて殲滅
                                        + (18 * Math.floor(this.coRewardBattle)); // シミュレーションルーム5-Cクリア
              //if (i == log) console.log("5時-" + (battleDayCase * Math.floor(this.coRewardBattle)));
              //if (i == log) console.log("5時-" + battleDayNum);
              //if (i == log) console.log("5時-" + Math.floor(this.lsWipeouts * this.coRewardBattle * 2));
              //if (i == log) console.log("5時-" + (18 * Math.floor(this.coRewardBattle)));
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleWeekCase * Math.floor(this.coRewardBattle)) // ウィークリー系課金アイテム(ケース)
                                        + battleWeekNum // デイリー系課金アイテム(個)
                                        + (36 * Math.floor(this.coRewardBattle)); // 鉄くずショップ
              //if (i == log) console.log("火曜-" + (battleWeekCase * Math.floor(this.coRewardBattle)));
              //if (i == log) console.log("火曜-" + battleWeekNum);
              //if (i == log) console.log("火曜-" + (36 * Math.floor(this.coRewardBattle)));
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleMonthCase * Math.floor(this.coRewardBattle)) // マンスリー系課金アイテム(ケース)
                                        + battleMonthNum; // マンスリー系課金アイテム(個)
            }
          }
          
          // コアダスト計算
          if (this.needs[i].NeedCore >= 0) {
            //if (i == log) console.log("コアダスト残り(計算前)：" + this.needs[i].NeedCore);
            flgCore = true;
            this.needs[i].HourCore++; // 残り時間を+1
            this.needs[i].NeedCore -= this.coRewardCore; // 必要経験値から1時間分の基地防御報酬を引く
            //if (i == log) console.log("-" + this.coRewardCore);
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCore -= (coreDayCase * Math.floor(this.coRewardCore)) // デイリー系課金アイテム(ケース)
                                      + coreDayNum // デイリー系課金アイテム(個)
                                      + Math.floor(this.lsWipeouts * this.coRewardCore * 2) // まとめて殲滅
                                      + 50; // デイリーミッション
              //if (i == log) console.log("5時-" + (coreDayCase * Math.floor(this.coRewardCore)));
              //if (i == log) console.log("5時-" + coreDayNum);
              //if (i == log) console.log("5時-" + Math.floor(this.lsWipeouts * this.coRewardCore * 2));
              //if (i == log) console.log("5時-" + 50);
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCore -= (coreWeekCase * Math.floor(this.coRewardCore)) // ウィークリー系課金アイテム(ケース)
                                      + coreWeekNum // デイリー系課金アイテム(個)
                                      + (36 * Math.floor(this.coRewardCore)) // 鉄くずショップ
              //if (i == log) console.log("火曜-" + (coreWeekCase * Math.floor(this.coRewardCore)));
              //if (i == log) console.log("火曜-" + coreWeekNum);
              //if (i == log) console.log("火曜-" + (36 * Math.floor(this.coRewardCore)));
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCore -= (coreMonthCase * Math.floor(this.coRewardCore)) // ウィークリー系課金アイテム(ケース)
                                      + coreMonthNum; // マンスリー系課金アイテム(個)
              //if (i == log) console.log("月初-" + (coreMonthCase * Math.floor(this.coRewardCore)));
              //if (i == log) console.log("月初-" + coreMonthNum);
            }
            //if (i == log) console.log("コアダスト残り(計算後)：" + this.needs[i].NeedCore);
          }

          // 課金額計算
          if (nextday.getHours() == 5) {
            //if (i == 0) console.log("aaaa")
            if (flgCredit || flgBattle || flgCore) {
              //if (i == 0) console.log("bbbb")
              //if (this.needs[i].NeedCredit >= 0 && this.needs[i].NeedBattle >= 0 && this.needs[i].NeedCore >= 0) {
                //if (i == 0) console.log("cccc")
                this.packs.forEach(function(pack){
                  if (pack.check == true) {
                    // 5時を迎えた時
                    if (nextday.getHours() == 5 && pack.division == "デイリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 0) console.log("デイリー：クレ：" + pack.price)
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 0) console.log("デイリー：バト：" + pack.price)
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 0) console.log("デイリー：コア：" + pack.price)
                      }
                    }

                    // 火曜の5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDay() == 2 && pack.division == "ウィークリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 0) console.log("ウィークリー：クレ：" + pack.price)
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 0) console.log("ウィークリー：バト：" + pack.price)
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 0) console.log("ウィークリー：コア：" + pack.price)
                      }
                    }
                    
                    // 月初めの5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.division == "マンスリー") {
                      //if (pack.name == "30-DAY成長補給品") console.log("30-DAY成長補給品")
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 1) console.log("マンスリー：クレ：" + pack.price)
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 1) console.log("マンスリー：バト：" + pack.price)
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 1) console.log("マンスリー：コア：" + pack.price)
                      }
                    }
                    
                    // 月初めの5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.name == "30-DAY成長補給品") {
                      if (flgCore) {
                        _this.needs[i].NeedYen += pack.price;
                        //if (i == 1) console.log("マンスリー：コア：" + pack.price)
                      }
                    }
                  }
                })
              //}
            }
          }
        };

        // 所要日数
        this.needs[i].DayCredit = this.needs[i].HourCredit / 24;
        this.needs[i].DayBattle = this.needs[i].HourBattle / 24;
        this.needs[i].DayCore   = this.needs[i].HourCore / 24;

        // 所要日数グラフ
        this.needs[i].DayCreditPer = Math.floor(this.needs[i].DayCredit / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayBattlePer = Math.floor(this.needs[i].DayBattle / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayCorePer   = Math.floor(this.needs[i].DayCore   / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)

        // 最短日数
        let today = new Date();
        let maxHour = Math.max(this.needs[i].HourCredit, this.needs[i].HourBattle, this.needs[i].HourCore);
        today.setHours(today.getHours() + maxHour);
        this.needs[i].ArrivalDate = (today.getFullYear()+"").slice(-2) + "/" + ("00"+(today.getMonth()+1)).slice(-2) + "/" + ("00"+today.getDate()).slice(-2) + "(" + [ "日", "月", "火", "水", "木", "金", "土" ][today.getDay()] + ")" + ("00"+today.getHours()).slice(-2) + "時";
        //console.log("maxHour=" + maxHour);
        //console.log("today.getDate()=" + today.getDate());

        // 現在日時
        this.lsToday = new Date();
        this.lsToday = "現在：" + (this.lsToday.getFullYear()+"").slice(-2) + "/" + ("00"+(this.lsToday.getMonth()+1)).slice(-2) + "/" + ("00"+this.lsToday.getDate()).slice(-2) + "(" + [ "日", "月", "火", "水", "木", "金", "土" ][this.lsToday.getDay()] + ")" + ("00"+this.lsToday.getHours()).slice(-2) + "時";
      }

      for (let i = lpCnt+1; i <= 2; ++i) {
        // 所要日数
        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].DayCredit = 0;
        this.needs[i].DayBattle = 0;
        this.needs[i].DayCore = 0;
        this.needs[i].DayCreditPer = 0;
        this.needs[i].DayBattlePer = 0;
        this.needs[i].DayCorePer = 0;
        this.needs[i].ArrivalDate = "必要資材データ無し";
        this.needs[i].NeedYen = 0;
      }

    },

    mChangeNikkeLv() { // ニケレベル変更
      if (this.coNikkeLv < 1) {
        this.coNikkeLv = 1;
      } else if (this.coNikkeLv > this.experience.length) {
        this.coNikkeLv = this.experience.length;
      } else {
        this.coNikkeLv = Math.floor(this.coNikkeLv);
      }
      this.$cookies.set('coNikkeLv', this.coNikkeLv);
      this.mCalcLeveling();
    },

    mChangeRewardLv() { // 基地防御レベル変更
      if (this.coRewardLv < 1) {
        this.coRewardLv = 1;
      } else if (this.coRewardLv > this.reward.length) {
        this.coRewardLv = this.reward.length;
      } else {
        this.coRewardLv = Math.floor(this.coRewardLv);
      }
      var found = this.reward.find(e => e.Lv === this.coRewardLv);
      this.coRewardCredit = Math.floor(found.Credit*1.6);
      this.coRewardBattle = Math.floor(found.Battle*1.45);
      this.coRewardCore = Math.floor(found.Core*1.45 * Math.pow(10,2)) / Math.pow(10,2);
      //console.log(found.Core);
      this.$cookies.set('coRewardLv', this.coRewardLv);
      this.$cookies.set('coRewardCredit', this.coRewardCredit);
      this.$cookies.set('coRewardBattle', this.coRewardBattle);
      this.$cookies.set('coRewardCore', this.coRewardCore);
      this.mCalcLeveling();
    },

    mChangeWipeouts() { // まとめて殲滅回数変更
      if (this.lsWipeouts < 1) {
        this.lsWipeouts = 1;
      } else if (this.lsWipeouts > 11) {
        this.lsWipeouts = 11;
      } else {
        this.lsWipeouts = Math.floor(this.lsWipeouts);
      }
      this.$cookies.set('lsWipeouts', this.lsWipeouts);
      this.mCalcLeveling();
    },

    mChangePosCaseCredit() { // クレジットケース数量変更
      if (this.lsPosCaseCredit < 0) {
        this.lsPosCaseCredit = 0;
      } else {
        this.lsPosCaseCredit = Math.floor(this.lsPosCaseCredit);
      }
      this.$cookies.set('lsPosCaseCredit', this.lsPosCaseCredit);
      this.mCalcLeveling();
    },

    mChangePosCredit() { // クレジット数量変更
      if (this.lsPosCredit < 0) {
        this.lsPosCredit = 0;
      } else {
        this.lsPosCredit = Math.floor(this.lsPosCredit);
      }
      this.$cookies.set('lsPosCredit', this.lsPosCredit);
      this.mCalcLeveling();
    },

    mChangePosCaseBattle() { // バトルデータケース数量変更
      if (this.lsPosCaseBattle < 0) {
        this.lsPosCaseBattle = 0;
      } else {
        this.lsPosCaseBattle = Math.floor(this.lsPosCaseBattle);
      }
      this.$cookies.set('lsPosCaseBattle', this.lsPosCaseBattle);
      this.mCalcLeveling();
    },

    mChangePosBattle() { // バトルデータ数量変更
      if (this.lsPosBattle < 0) {
        this.lsPosBattle = 0;
      } else {
        this.lsPosBattle = Math.floor(this.lsPosBattle);
      }
      this.$cookies.set('lsPosBattle', this.lsPosBattle);
      this.mCalcLeveling();
    },

    mChangePosCaseCore() { // コアダストケース数量変更
      if (this.lsPosCaseCore < 0) {
        this.lsPosCaseCore = 0;
      } else {
        this.lsPosCaseCore = Math.floor(this.lsPosCaseCore);
      }
      this.$cookies.set('lsPosCaseCore', this.lsPosCaseCore);
      this.mCalcLeveling();
    },

    mChangePosCore() { // コアダスト数量変更
      if (this.lsPosCore < 0) {
        this.lsPosCore = 0;
      } else {
        this.lsPosCore = Math.floor(this.lsPosCore);
      }
      this.$cookies.set('lsPosCore', this.lsPosCore);
