    mMenu(menuName, btnName) { // メニュー表示ON/OFF
      let menu = document.getElementById(menuName);
      let menuBtn = document.getElementById(btnName);
      if (menu.style.display == "") {
        menu.style.display = "none";
        menuBtn.open = ""
      } else {
        menu.style.display = ""
        menuBtn.open = "open"
      }
    },

    mSaveQf(tag) { // クッキーから部隊欄を保存
      // ★類似処理がmounted内にもあるため修正時はそちらも見直す事
      let ret = true;
      if (tag != "auto") {
        ret = window.confirm("現在の部隊欄を設定"+tag+"にセーブします。\n上書きして宜しいですか？"); // 部隊編成の部隊欄から候補一覧へニケを戻す
      };

      let buf = "";
      for (let i = 1; i <= 6; ++i) {
      let squad = document.getElementById("squad"+i);
        squad.childNodes.forEach(function(menber){
          buf = buf + menber.name + ",";
        });
        this.$cookies.set('squad'+i+"_"+tag, buf);
      };
    },

    mLoadQf(tag) { // クッキーから部隊欄を読み取り再現する
      let ret = true;
      if (tag != "auto") {
        ret = this.mClearQf("設定"+tag+"をロードします。\n部隊欄をクリアしますが宜しいですか？"); // 部隊編成の部隊欄から候補一覧へニケを戻す
      };

      if (ret) {
        let buf = "";
        let menbers = [];
        for (let i = 1; i <= 6; ++i) {
          buf = this.$cookies.get('squad'+i+"_"+tag);
          let squad = document.getElementById("squad" + i);
          if (buf) {
            menbers = buf.split(",");
            menbers.forEach(function(menber){
              candidate.childNodes.forEach(function(nikke){
                if (nikke.name == menber) {
                  squad.appendChild(nikke);
                }
              });
            });
          }
        }
      }
      this.mSaveQf("auto");
    },

    mClearQf(msg) { // 部隊編成の部隊欄から候補一覧へニケを戻す
      let ret = false;
      if (msg != "") {
        ret = window.confirm(msg);
      }

      if (ret) {
        let candidate = document.getElementById("candidate");
        for (let i = 1; i <= 6; ++i) {
          let squad = document.getElementById("squad" + i);
          while (squad.childNodes.length != 0) {
            squad.childNodes.forEach(function(menber){
              let idx = 0; // ドロップ先の要素番号
              let cnt = 0;
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              candidate.childNodes.forEach(function(item){
                if(item.id <= menber.id) {
                  idx = cnt+1;
                }
                cnt++;
              });
              candidate.insertBefore(menber, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加
            });
          };
        };
        this.mSaveQf("auto");
      };
      return ret;
    },

    mClearSm(msg) { // 散布図メーカーのグラフから候補一覧へニケを戻す
      let ret = false;
      if (msg != "") {
        ret = window.confirm(msg);
      }

      if (ret) {
        let candidate = document.getElementById("smCandidate");
        let graph = document.getElementById("smGraph");
        let flgFind = true;
        console.log("graph.childNodes.length=" + graph.childNodes.length)
        while (flgFind) {
          flgFind = false;
          graph.childNodes.forEach(function(menber){
            //console.log("menber=" + menber)
            //console.log("menber.id=" + menber.id)
            if (menber.id === undefined) { // undefinedの要素はスキップ
            } else if (menber.id.includes("nikke")) {
              flgFind = true;
              let idx = 0; // ドロップ先の要素番号
              let cnt = 0;
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              candidate.childNodes.forEach(function(item){
                if(item.id <= menber.id) {
                  idx = cnt+1;
                }
                cnt++;
              });
              candidate.insertBefore(menber, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加

              // ドロップした画像のstyleを元に戻す
              menber.style.top = "0px";
              menber.style.left = "0px";
              menber.style.zIndex = 1;
              menber.style.position = "relative";
            };
          });
        };
        console.log("graph.childNodes.length=" + graph.childNodes.length)
      };
      return ret;
    },

    mQfImgChange() { // ニケ画像の格納先フォルダ変更
      if(this.qfImgFolder == "default") {
        this.qfImgFolder = "burst";
      } else {
        this.qfImgFolder = "default";
      }      
    },

    mSkillInfoClose(skillId, nikkeId) { // 部隊編成 - スキル詳細を消す
      //console.log("--------------------------------")
      //console.log("skillId=" + skillId)
      //console.log("nikkeId=" + nikkeId)
      //let target = document.getElementById(skillInfoId);
      //target.parentNode.parentNode.style.display = "none"; // スキル詳細を非表示化
      
      // 絞り込み対象スキル情報の配列から×ボタンが押されたものを消す
      let cnt = 0;
      let idx = 0;
      this.skillInfos.forEach(function(skillInfo){
        if(skillInfo.id == skillId) {
          //alert("remove:" + cnt)
          idx = cnt;
        }
        cnt++;
      });
      this.skillInfos.splice(idx, 1);

      // 部隊欄に編成済みのニケではない場合、画像を非表示化
      if (!document.getElementById(nikkeId).parentNode.id.startsWith('squad')) {
        document.getElementById(nikkeId).style.display = "none";
      } 
    },

