              if(reg.test(nikke.burst))  burst  = burst.replace(reg, '<mark>$1</mark>');
              flgFind = true;
            }
          }
          if(keyword.length >= 2 || keyword[0] != '') {
            for (let cnt = 0; cnt < keyword.length; cnt++) {
              if((nikke.skill1.includes(keyword[cnt]) || nikke.skill2.includes(keyword[cnt]) || nikke.burst.includes(keyword[cnt]))) {
