                if(nikke.burst.includes(keyword[cnt]))  burst  = burst.replace(keyword[cnt], '<mark>'+keyword[cnt]+'</mark>');
                flgFind = true;
              }
            };
          }

          if(flgFind) {
            let reg = /(<mark>)/i;
            if(!reg.test(skill1)) skill1 = "";
            if(!reg.test(skill2)) skill2 = "";
            if(!reg.test(burst)) burst = "";
            //alert(_this.skillInfos.length)
            _this.skillInfos.push({ id: _this.skillInfos.length, nikkeId: nikke.id, name: skillInfoName, skill1: skill1.replace(regRet, "<br>"), skill2: skill2.replace(regRet, "<br>"), burst: burst.replace(regRet, "<br>") });
          } else {
            disp = 'none';
          }
        }

        if (flgCls == 2) {
          if (document.getElementById("sm"+nikke.id).parentNode.id.startsWith('smGraph')) disp = ''; // グラフに配置されたニケは強制表示
          document.getElementById("sm"+nikke.id).style.display = disp;
        } else {
          if (document.getElementById(nikke.id).parentNode.id.startsWith('squad')) disp = ''; // 部隊欄に配置されたニケは強制表示
          document.getElementById(nikke.id).style.display = disp;
        }        
      })
