    mNikkeDbl(id, parentId, toId) { // 部隊作成でニケ画像をダブルクリック
      let target = document.getElementById(id);
      // 部隊欄上に画像がある場合は画像を候補へ移す
      if (target.parentNode.id.includes(parentId)) {
        let candidate = document.getElementById(toId);
        let idx = 0; //candidate.childNodes.length; // ドロップ先の要素番号（デフォルトは先頭）
        let cnt = 0;
        // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
        candidate.childNodes.forEach(function(item){
            if(item.id <= target.id) {
              //alert("id:" + item.id +" "+ target.id)
              idx = cnt+1;
            }
            cnt++;
          });
        candidate.insertBefore(target, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加
        
        // styleを元に戻す
        target.style.top = "0px";
        target.style.left = "0px";
        target.style.zIndex = 1;
        target.style.position = "relative";

        this.mSaveQf("auto");
      }
    },

