import Vue from 'vue'
import App from './App'
import VueCookies from 'vue-cookies'
import './assets/style/common.css'; // 共通
import './assets/style/billing.css'; // 課金シミュレーション
import './assets/style/rapid.css'; // 速射訓練
import './assets/style/squad.css'; // 部隊編成
import './assets/style/scatter.css'; // 散布図メーカー
import './assets/style/leveling.css'; // レベリングシミュレーション

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App)
})

Vue.use(VueCookies)



